import React, { Component } from 'react';
import { Row, Col, Card, Divider } from 'antd';
import { BtnSave } from 'components/common/button';
import TableWarningSelected from './TableWarningSelected';
import TableWarningNotSelected from './TableWarningNotSelected';

class TabAcademicWarning extends Component {
  state = {};
  render() {
    return (
      <Card>
        <Divider>Danh sách sinh viên chưa chọn</Divider>
        <Row>
          <Col span={12}>
            <div>
              Số sinh viên cảnh báo thôi học: <b>0</b>
            </div>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }} />
        </Row>
        <Row>
          <Col span={24}>
            <TableWarningNotSelected />
          </Col>
        </Row>
        <Divider>Danh sách sinh viên đã chọn</Divider>
        <Row>
          <Col span={12}>
            <div>
              Tổng số sinh viên: <b>0</b>
            </div>
          </Col>
          <Col span={12} style={{ textAlign: 'right' }}>
            <BtnSave title="In cảnh cáo thôi học" />
          </Col>
        </Row>
        <Row className="rowMargin">
          <Col span={24}>
            <TableWarningSelected />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default TabAcademicWarning;



